@charset "UTF-8";
@font-face {
    font-family: 'Alaska';
    src: url("../fonts/Alaska-Regular.woff2") format("woff2"), url("../fonts/Alaska-Regular.ttf") format("ttf");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Alaska';
    src: url("../fonts/Alaska-SemiBold.woff2") format("woff2"), url("../fonts/Alaska-SemiBold.ttf") format("ttf");
    font-weight: 500;
    font-style: normal;
}

:root {
    --breakpoint: xxl;
    --font: 'Alaska', sans-serif;
    --font-secondary: 'Inter', sans-serif;
    --font-size: 16px;
    --line-height: 1.5;
    --paragraph-spacing: 1.5rem;
    --font-size-xs: 14px;
    --font-size-sm: 18px;
    --font-size-md: 20px;
    --font-size-lg: 30px;
    --font-size-xl: 36px;
    --font-size-2xl: 48px;
    --font-size-3xl: 70px;
    --h0: 60px;
    --h1: 70px;
    --h2: 48px;
    --h3: 36px;
    --h4: 30px;
    --h5: 20px;
    --h6: 18px;
    --font-light: 300;
    --font-regular: 400;
    --font-semibold: 500;
    --color: #000;
    --bg-color: #fff;
    --primary: #F4F2F0;
    --secondary: #666666;
    --light: #f2f2f2;
    --dark: #212529;
    --gray-light: #f2f2f2;
    --gray: #999;
    --gray-dark: #666;
    --border-color: #dfdfdf;
    --img-bg: var(--gray-light);
    --green: #7ab573;
    --orange: #f6b036;
    --red: #de594a;
    --info: var(--light);
    --success: var(--green);
    --warning: var(--orange);
    --error: var(--red);
    --spacing: 8px;
    --gutter-x: 50px;
    --gutter-y: 50px;
    --container-width: 100%;
    --container-padding: 50px;
    --section-padding: 65px;
    --border-radius: 5px;
}

@media (max-width: 1399.98px) {
    :root {
        --breakpoint: xl;
    }
}

@media (max-width: 1279.98px) {
    :root {
        --breakpoint: lg;
    }
}

@media (max-width: 991.98px) {
    :root {
        --breakpoint: md;
        --container-padding: 30px;
        --section-padding: 80px;
    }
}

@media (max-width: 767.98px) {
    :root {
        --breakpoint: sm;
    }
}

@media (max-width: 575.98px) {
    :root {
        --breakpoint: xs;
        --section-padding: 60px;
    }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
    display: block;
}

ol,
ul {
    list-style: none;
}

q,
blockquote {
    quotes: none;
}

q:before, q:after,
blockquote:before,
blockquote:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

th {
    text-align: inherit;
    text-align: -webkit-match-parent;
}

[tabindex='-1']:focus:not(:focus-visible) {
    outline: 0 !important;
    /* stylelint-disable-line declaration-no-important */
}

input,
button,
textarea,
select {
    font: inherit;
}

button {
    cursor: pointer;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

svg {
    display: block;
    fill: currentColor;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

::selection {
    background: #fff;
}

html {
    height: 100%;
    font-size: var(--font-size);
    overflow-x: hidden;
}

body {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
    font-family: var(--font);
    line-height: var(--line-height);
    color: var(--color);
    background-color: var(--primary);
    overflow: hidden;
}

a.hover-underlined:hover {
    text-decoration: underline;
}

a.hover-grayed:hover {
    color: var(--gray);
}

.pb-0 {
    padding-bottom: 0 !important;
}

.pt-0 {
    padding-top: 0 !important;
}

.pl-0 {
    padding-left: 0 !important;
}

.pr-0 {
    padding-right: 0 !important;
}

.m-0 {
    margin: 0 !important;
}

.mb-0 {
    margin-bottom: 0 !important;
}

.mt-0 {
    margin-top: 0 !important;
}

.ml-0 {
    margin-left: 0 !important;
}

.ml-auto {
    margin-left: auto !important;
}

.mr-0 {
    margin-right: 0 !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font, var(--font-secondary));
    line-height: 1.3;
    margin: 0 0 1rem;
}

h1 {
    font-size: var(--h1);
}

h2 {
    font-size: var(--h2);
}

h3 {
    font-size: var(--h3);
}

h4 {
    font-size: var(--h4);
}

h5 {
    font-size: var(--h5);
}

h6 {
    font-size: var(--h6);
}

p,
address {
    margin-bottom: var(--paragraph-spacing);
}

a {
    color: currentColor;
    text-decoration: none;
    /* &:active{
        outline: none;
        -moz-outline-style: none;
    } */
