.apply-formatting > *:first-child {
    margin-top: 0 !important;
}

.apply-formatting > *:last-child {
    margin-bottom: 0 !important;
}

.apply-formatting h2,
.apply-formatting h3,
.apply-formatting h4,
.apply-formatting h5,
.apply-formatting h6 {
    margin-top: calc(var(--paragraph-spacing) * 1.5);
}

.apply-formatting p {
    margin-bottom: var(--paragraph-spacing);
}

.apply-formatting a:not(.button) {
    color: var(--color);
    text-decoration: underline;
}

.apply-formatting a:not(.button):hover {
    text-decoration: none;
}

.apply-formatting ul {
    margin-bottom: var(--paragraph-spacing);
}

.apply-formatting ul li {
    position: relative;
    padding-left: 20px;
    margin-bottom: 5px;
}

.apply-formatting ul li:last-child {
    margin-bottom: 0;
}

.apply-formatting ul li:before {
    content: '';
    position: absolute;
    width: 5px;
    height: 5px;
    left: 0;
    top: .6em;
    background-color: var(--primary);
    border-radius: 50%;
}

.apply-formatting ol {
    counter-reset: ol-counter;
    margin-bottom: var(--paragraph-spacing);
}

.apply-formatting ol li {
    position: relative;
    counter-increment: ol-counter;
    padding-left: 20px;
    margin-bottom: 5px;
}

.apply-formatting ol li:last-child {
    margin-bottom: 0;
}

.apply-formatting ol li:before {
    content: counter(ol-counter) ") ";
    position: absolute;
    top: 0;
    left: 0;
}

.apply-formatting hr {
    display: block;
    width: 100%;
    height: 0;
    border: 1px solid var(--border-color);
    border-width: 1px 0 0 0;
    margin: var(--paragraph-spacing) 0;
}

.apply-formatting img {
    width: 100%;
    margin-bottom: var(--paragraph-spacing);
}

.apply-formatting .video-wrapper {
    margin-bottom: var(--paragraph-spacing);
}

.default__link {
    text-decoration: underline;
}

.default__link:hover {
    text-decoration: none;
}

.lazyload,
.lazyloading,
.lazyload.lazyloaded,
.lazyloading.lazyloaded {
    opacity: 0;
}

.lazyloaded {
    opacity: 1;
    transition: opacity .5s;
}

.ls-is-cached {
    opacity: 1;
    transition: opacity 0s;
}

img[data-sizes="auto"] {
    width: 100%;
}

:root {
    --f-spinner-width: 36px;
    --f-spinner-height: 36px;
    --f-spinner-color-1: rgba(0, 0, 0, 0.1);
    --f-spinner-color-2: rgba(17, 24, 28, 0.8);
    --f-spinner-stroke: 2.75;
}

.f-spinner {
    margin: auto;
    padding: 0;
    width: var(--f-spinner-width);
    height: var(--f-spinner-height);
}

.f-spinner svg {
    width: 100%;
    height: 100%;
    vertical-align: top;
    animation: f-spinner-rotate 2s linear infinite;
}

.f-spinner svg * {
    stroke-width: var(--f-spinner-stroke);
    fill: none;
}

.f-spinner svg *:first-child {
    stroke: var(--f-spinner-color-1);
}

.f-spinner svg *:last-child {
    stroke: var(--f-spinner-color-2);
    animation: f-spinner-dash 2s ease-in-out infinite;
}

@keyframes f-spinner-rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes f-spinner-dash {
    0% {
        stroke-dasharray: 1,150;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 90,150;
        stroke-dashoffset: -35;
    }
    100% {
        stroke-dasharray: 90,150;
        stroke-dashoffset: -124;
    }
}

.f-throwOutUp {
    animation: .175s ease-out both f-throwOutUp;
}

.f-throwOutDown {
    animation: .175s ease-out both f-throwOutDown;
}

@keyframes f-throwOutUp {
    to {
        transform: translate3d(0, -150px, 0);
        opacity: 0;
    }
}

@keyframes f-throwOutDown {
    to {
        transform: translate3d(0, 150px, 0);
        opacity: 0;
    }
}

.f-zoomInUp {
    animation: var(--f-transition-duration, 0.2s) ease-out 0.1s both f-zoomInUp;
}

.f-zoomOutDown {
    animation: var(--f-transition-duration, 0.2s) ease-out both f-zoomOutDown;
}

@keyframes f-zoomInUp {
    from {
        transform: scale(0.975) translate3d(0, 16px, 0);
        opacity: 0;
    }
    to {
        transform: scale(1) translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes f-zoomOutDown {
    to {
        transform: scale(0.975) translate3d(0, 16px, 0);
        opacity: 0;
    }
}

.f-fadeIn {
    animation: var(--f-transition-duration, 0.2s) ease both f-fadeIn;
    z-index: 2;
}

.f-fadeOut {
    animation: var(--f-transition-duration, 0.2s) ease both f-fadeOut;
    z-index: 1;
}

@keyframes f-fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes f-fadeOut {
    100% {
        opacity: 0;
    }
}

.f-fadeSlowIn {
    animation: var(--f-transition-duration, 0.5s) ease both f-fadeSlowIn;
    z-index: 2;
}

.f-fadeSlowOut {
    animation: var(--f-transition-duration, 0.5s) ease both f-fadeSlowOut;
    z-index: 1;
}

@keyframes f-fadeSlowIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes f-fadeSlowOut {
    100% {
        opacity: 0;
    }
}

.f-fadeFastIn {
    animation: var(--f-transition-duration, 0.2s) ease-out both f-fadeFastIn;
    z-index: 2;
}

.f-fadeFastOut {
    animation: var(--f-transition-duration, 0.2s) ease-out both f-fadeFastOut;
    z-index: 2;
}

@keyframes f-fadeFastIn {
    0% {
        opacity: .75;
    }
    100% {
        opacity: 1;
    }
}

@keyframes f-fadeFastOut {
    100% {
        opacity: 0;
    }
}

.f-crossfadeIn {
    animation: var(--f-transition-duration, 0.2s) ease-out both f-crossfadeIn;
    z-index: 2;
}

.f-crossfadeOut {
    animation: calc(var(--f-transition-duration, 0.2s)*.5) linear 0.1s both f-crossfadeOut;
    z-index: 1;
}

@keyframes f-crossfadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes f-crossfadeOut {
    100% {
        opacity: 0;
    }
}

.f-slideIn.from-next {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-slideInNext;
}

.f-slideIn.from-prev {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-slideInPrev;
}

.f-slideOut.to-next {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-slideOutNext;
}

.f-slideOut.to-prev {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-slideOutPrev;
}

@keyframes f-slideInPrev {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translate3d(0, 0, 0);
    }
}

@keyframes f-slideInNext {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translate3d(0, 0, 0);
    }
}

@keyframes f-slideOutNext {
    100% {
        transform: translateX(-100%);
    }
}

@keyframes f-slideOutPrev {
    100% {
        transform: translateX(100%);
    }
}

.f-classicIn.from-next {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-classicInNext;
    z-index: 2;
}

.f-classicIn.from-prev {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-classicInPrev;
    z-index: 2;
}

.f-classicOut.to-next {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-classicOutNext;
    z-index: 1;
}

.f-classicOut.to-prev {
    animation: var(--f-transition-duration, 0.85s) cubic-bezier(0.16, 1, 0.3, 1) f-classicOutPrev;
    z-index: 1;
}

@keyframes f-classicInNext {
    0% {
        transform: translateX(-75px);
        opacity: 0;
    }
    100% {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes f-classicInPrev {
    0% {
        transform: translateX(75px);
        opacity: 0;
    }
    100% {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

@keyframes f-classicOutNext {
    100% {
        transform: translateX(-75px);
        opacity: 0;
    }
}

@keyframes f-classicOutPrev {
    100% {
        transform: translateX(75px);
        opacity: 0;
    }
}

:root {
    --f-button-width: 40px;
    --f-button-height: 40px;
    --f-button-border: 0;
    --f-button-border-radius: 0;
    --f-button-color: #374151;
    --f-button-bg: #f8f8f8;
    --f-button-hover-bg: #e0e0e0;
    --f-button-active-bg: #d0d0d0;
    --f-button-shadow: none;
    --f-button-transition: all 0.15s ease;
    --f-button-transform: none;
    --f-button-svg-width: 20px;
    --f-button-svg-height: 20px;
    --f-button-svg-stroke-width: 1.5;
    --f-button-svg-fill: none;
    --f-button-svg-filter: none;
    --f-button-svg-disabled-opacity: 0.65;
}

.f-button {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    position: relative;
    margin: 0;
    padding: 0;
    width: var(--f-button-width);
    height: var(--f-button-height);
    border: var(--f-button-border);
    border-radius: var(--f-button-border-radius);
    color: var(--f-button-color);
    background: var(--f-button-bg);
    box-shadow: var(--f-button-shadow);
    pointer-events: all;
    cursor: pointer;
    transition: var(--f-button-transition);
}

@media (hover: hover) {
    .f-button:hover:not([disabled]) {
        color: var(--f-button-hover-color);
        background-color: var(--f-button-hover-bg);
    }
}

.f-button:active:not([disabled]) {
    background-color: var(--f-button-active-bg);
}

.f-button:focus:not(:focus-visible) {
    outline: none;
}

.f-button:focus-visible {
    outline: none;
    box-shadow: inset 0 0 0 var(--f-button-outline, 2px) var(--f-button-outline-color, var(--f-button-color));
}

.f-button svg {
    width: var(--f-button-svg-width);
    height: var(--f-button-svg-height);
    fill: var(--f-button-svg-fill);
    stroke: currentColor;
    stroke-width: var(--f-button-svg-stroke-width);
    stroke-linecap: round;
    stroke-linejoin: round;
    transition: opacity .15s ease;
    transform: var(--f-button-transform);
    filter: var(--f-button-svg-filter);
    pointer-events: none;
}

.f-button[disabled] {
    cursor: default;
}

.f-button[disabled] svg {
    opacity: var(--f-button-svg-disabled-opacity);
}

.f-carousel__nav .f-button.is-prev, .f-carousel__nav .f-button.is-next, .fancybox__nav .f-button.is-prev, .fancybox__nav .f-button.is-next {
    position: absolute;
    z-index: 1;
}

.is-horizontal .f-carousel__nav .f-button.is-prev, .is-horizontal .f-carousel__nav .f-button.is-next, .is-horizontal .fancybox__nav .f-button.is-prev, .is-horizontal .fancybox__nav .f-button.is-next {
    top: 50%;
    transform: translateY(-50%);
}

.is-horizontal .f-carousel__nav .f-button.is-prev, .is-horizontal .fancybox__nav .f-button.is-prev {
    left: var(--f-button-prev-pos);
}

.is-horizontal .f-carousel__nav .f-button.is-next, .is-horizontal .fancybox__nav .f-button.is-next {
