.ratio {
    position: relative;
    width: 100%;
}

.ratio:before {
    content: '';
    display: block;
    pointer-events: none;
    padding-top: var(--aspect-ratio, 56.25%);
}

.ratio > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.cover,
.contain {
    position: relative;
    width: 100%;
}

.cover:before,
.contain:before {
    content: '';
    display: block;
    pointer-events: none;
    padding-top: var(--aspect-ratio, 66.66%);
}

.cover > img,
.contain > img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.contain > img {
    object-fit: contain;
}

