.preloader {
    display: none;
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
}

.preloader__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.preloader__loader {
    display: block;
    margin: 0 auto 0.625em;
    width: 1.875em;
    height: 1.875em;
}

.preloader__loader:before {
    content: '';
    display: block;
    width: 1.875em;
    height: 1.875em;
    border-radius: 50%;
    border: solid 3px rgba(0, 0, 0, 0.25);
