.s2022-cookie-bar .cb-button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    min-height: 50px;
    padding: 0 25px;
    line-height: 1.5;
    cursor: pointer;
    text-decoration: none;
    -webkit-appearance: none;
    outline: none;
    box-shadow: none;
    font-family: inherit;
    overflow: hidden;
    font-weight: var(--text-fw);
